import React, { useState } from 'react';
import { useNavigate, NavLink } from 'react-router-dom';
import { useGame } from '../contexts/GameContext';
import '../styles/Header.css';

function Header() {
    const navigate = useNavigate();
    const {
        mainCompanyMoney,
        researchPoints,
        saveGameState,
        resetGame
    } = useGame();

    const [isMenuOpen, setIsMenuOpen] = useState(false);

    const handleSaveGame = () => {
        saveGameState();
        const saveBtn = document.getElementById('saveGame');
        if (saveBtn) {
            const originalText = saveBtn.innerHTML;
            saveBtn.innerHTML = ' Guardado';
            saveBtn.style.background = '#4caf50';
            setTimeout(() => {
                saveBtn.innerHTML = originalText;
                saveBtn.style.background = '#2196f3';
            }, 2000);
        }
    };

    const handleResetGame = () => {
        if (window.confirm('Ests seguro que deseas reiniciar el juego? Perders todo tu progreso!')) {
            resetGame();
        }
    };

    const toggleMenu = () => {
        setIsMenuOpen(!isMenuOpen);
    };

    return (
        <header className="header">
            <div className="header-content">
                <div className="header-top">
                    <div className="logo" onClick={() => navigate('/')}>
                        <h1>Tycoon</h1>
                    </div>

                    <div className="main-stats-container">
                        <div className="main-stats">
                            <div className="stat">
                                <img src={process.env.PUBLIC_URL + "/media/usd-circle.svg"} alt="Money" className="resource-icon" />
                                <span>{mainCompanyMoney.toFixed(2)}</span>
                            </div>
                            <div className="stat">
                                <img src={process.env.PUBLIC_URL + "/media/sparkles.svg"} alt="Research" className="resource-icon" />
                                <span>{researchPoints}</span>
                            </div>
                        </div>
                    </div>

                    <button className="mobile-menu-btn" onClick={toggleMenu}>
                        <span className={`hamburger ${isMenuOpen ? 'open' : ''}`}></span>
                    </button>
                </div>

                <nav className={`nav ${isMenuOpen ? 'open' : ''}`}>
                    <NavLink
                        to="/"
                        className={({ isActive }) => `nav-btn ${isActive ? 'active' : ''}`}
                        onClick={() => setIsMenuOpen(false)}
                    >
                        <img src={process.env.PUBLIC_URL + "/media/companies-icon.svg"} alt="" className="nav-icon" />
                        Compaas
                    </NavLink>
                    <NavLink
                        to="/investigation"
                        className={({ isActive }) => `nav-btn ${isActive ? 'active' : ''}`}
                        onClick={() => setIsMenuOpen(false)}
                    >
                        <img src={process.env.PUBLIC_URL + "/media/technology-icon.svg"} alt="" className="nav-icon" />
                        Investigacin
                    </NavLink>
                    <NavLink
                        to="/rrhh"
                        className={({ isActive }) => `nav-btn ${isActive ? 'active' : ''}`}
                        onClick={() => setIsMenuOpen(false)}
                    >
                        <img src={process.env.PUBLIC_URL + "/media/human-resources-icon.svg"} alt="" className="nav-icon" />
                        RRHH
                    </NavLink>
                    <NavLink
                        to="/statistics"
                        className={({ isActive }) => `nav-btn ${isActive ? 'active' : ''}`}
                        onClick={() => setIsMenuOpen(false)}
                    >
                        <img src={process.env.PUBLIC_URL + "/media/statistics.svg"} alt="" className="nav-icon" />
                        Estadsticas
                    </NavLink>
                    <NavLink
                        to="/stock-market"
                        className={({ isActive }) => `nav-btn ${isActive ? 'active' : ''}`}
                        onClick={() => setIsMenuOpen(false)}
                    >
                        <img src={process.env.PUBLIC_URL + "/media/chart-line-up.svg"} alt="" className="nav-icon" />
                        Stock Market
                    </NavLink>
                </nav>

                <div className={`header-actions ${isMenuOpen ? 'open' : ''}`}>
                    <button id="saveGame" onClick={handleSaveGame} className="action-btn">
                        Guardar
                    </button>
                    <button onClick={handleResetGame} className="action-btn reset-btn">
                        Reiniciar
                    </button>
                </div>
            </div>
        </header>
    );
}

export default Header;
